.error-button{
    margin: 30px 0 0;
}
.error-indicator{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin: 20px 0 60px;
}
.error-indicator__title{
    font-size: 30px;
    text-align: center;
    color: #ffffff;
}
.error-indicator__text{
    display: block;
    width: 300px;
    color: #fff;
    text-align: center;
}
.header{
    margin-top: 20px;
    margin-bottom: 20px;
}
.header__title{

}
.header__link{
    margin: 0;
    padding: 0;
}
.header__nav{
    height: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.header__nav-link{
    position: relative;
    display: block;
    width: auto;
    height: 100%;
    padding: 4px 10px;
    border-radius: 4px;
    overflow: hidden;
}
.header__nav-link::after{
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgb(209, 179, 9); 
    z-index: -1;
    transition: all .2s ease-in-out;
}
.header__nav-link + .header__nav-link{
    margin-left: 10px;
}
.header__nav-link:hover{
    color: rgb(41, 41, 41);
}
.header__nav-link:hover::after{
    left: 0;
}
.active{
    color: rgb(41, 41, 41);
}
.active::after{
    left: 0; 
}
@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  @-webkit-keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  .lds-css{
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      z-index: 99;
  }
  .lds-double-ring {
    width:100%;
    height:100%;
    position: relative;
  }
  .lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 14px solid #000;
    border-color: #f9f9f9 transparent #f9f9f9 transparent;
    -webkit-animation: lds-double-ring 1s linear infinite;
    animation: lds-double-ring 1s linear infinite;
  }
  .lds-double-ring div:nth-child(2) {
    width: 128px;
    height: 128px;
    top: 36px;
    left: 36px;
    border-color: transparent #f5bb16 transparent #f5bb16;
    -webkit-animation: lds-double-ring_reverse 1s linear infinite;
    animation: lds-double-ring_reverse 1s linear infinite;
  }
  .lds-double-ring {
    width: 64px !important;
    height: 64px !important;
    -webkit-transform: translate(-32px, -32px) scale(0.32) translate(32px, 32px);
    transform: translate(-32px, -32px) scale(0.32) translate(32px, 32px);
  }
.random-planet{
    position: relative;
    min-height: 120px;
    padding: 15px;
    background-color: rgb(56, 56, 56);
    border-radius: 6px;
}
.random-planet__img{
    width: 100%;
    height: auto;
    border-radius: 8px;
}
.random-planet__title{
    color: #ffffff;
}
.random-planet__list{
    width: 100%;
    margin-top: 30px;
    list-style-type: none
}
.random-planet__item{
    background-color: transparent;
    border-color: rgb(70, 70, 70);
    color: #fff;
}
.random-planet__item span:last-of-type{
    display: inline-block;
    margin-left: 10px;
    font-weight: bold;
}
.list{
    position: relative;
    min-height: 200px;
}
.item-list{
    margin-top: 30px;
}
.item-list__name{
    background-color: rgb(56, 56, 56);
    color: #ffffff;
    transition: all 0.18s ease-in-out;
}
.item-list__name:hover{
    background-color: rgb(80, 80, 80);
    cursor: pointer;
}
.item-list__name:first-child{
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.item-list__name:last-child{
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}
.person-details{
    position: relative;
    min-height: 200px;
    margin-top: 30px;
    padding: 15px;
    background-color: rgb(56, 56, 56);
    border-radius: 6px;
}
.person-details__img{
    width: 100%;
    height: auto;
    border-radius: 8px;
}
.person-details__title{
    color: #ffffff;
}
.person-details__card{
    margin-top: 30px;
}
.person-details__name{
    border-color: rgb(70, 70, 70);
    background-color: rgb(56, 56, 56);
    color: #ffffff;
}
.person-details__name span:last-of-type{
    display: inline-block;
    margin-left: 10px;
    font-weight: bold;
}
.selected{
    display: block;
    color: #fff;
}
body{
    background-color: rgb(41, 41, 41);
}
h1, h2{
    color: #fff;
}
.stardb{
    max-width: 1000px;
